.section {
   &__reviews {
      padding: 0 0 100px;
   }
}
.reviews {
   &__header {
      max-width: 577px;
      width: 100%;
      margin: 0 auto 70px;
      @media (max-width: 600px){
         margin: 0 auto 40px;
      }
   }
   &__text {
      text-align: center;
   }
   &__swiper {
      max-width: 872px;
      width: 100%;
      margin-bottom: 50px;
      .slide {
         text-align: center;
         &__title {
            font-weight: 500 !important;
            color: #2b384c !important;
            margin-bottom: 24px;
         }
         &__title-name {
            font-family: "Poppins";
            margin-bottom: 8px;
         }
         &__text {
            font-family: "Poppins" !important;
            font-weight: 300 !important;
            color: #000000 !important;
            @media (max-width: 480px){
               font-size: 14px;
            }
         }
      }
   }
   .swiper-pagination {
      position: relative !important;
      bottom: 0 !important;
      display: flex;
      justify-content: center;
      align-items: center;
      &-bullet {
         width: 8px;
         height: 8px;
         opacity: 1 !important;
         background: #c3c7ce;
         transition: all 0.3s ease;
         &-active {
            width: 30px;
            height: 8px;
            background: #210c33;
            border-radius: 100px;
            transition: all 0.3s ease;
         }
      }
   }
}
