*,
*::before,
*::after {
	box-sizing: border-box;
}

html,
body {
	position: relative;
}

body {
	min-height: 100vh;
	height: 100%;
	display: flex;
	flex-direction: column;
	overflow-x: hidden !important ;
	font-style: normal;
	&.locked {
		overflow: hidden;
		background-color: #ffffff;
	}
}

main {
	flex: 1 1 auto;
	overflow: hidden !important ;
	position: relative;
	z-index: 0;
	padding-bottom: 50px;
	&::after {
		content: "";
		position: absolute;
		bottom: -25px;
		left: 0;
		right: 0;
		width: 100%;
		height: 150px;
		z-index: -1;
		background: #2b384c !important;
	}
	.decor {
		position: absolute;
		overflow: hidden;
		display: inline-block;
		@media (max-width: 992px){
			display: none;
		}
	}
	.decor1,
	.decor2 {
		filter: blur(300px);
		border-radius: 50%;
		transform: rotate(-130.44deg);
	}
	.decor1 {
		z-index: -2;
		top: -803px;
		right: -496px;
		width: 871.21px;
		height: 871.21px;
		background: rgba(218, 77, 241, 0.4);
	}
	.decor2 {
		z-index: -1;
		top: 95px;
		right: -948px;
		width: 1044.32px;
		height: 1044.32px;
		background: rgba(196, 245, 233, 0.7);
	}
	.decor3 {
		background: linear-gradient(
			181.87deg,
			rgba(255, 234, 198, 0.2) -157.44%,
			rgba(255, 234, 198, 0.144) 216.94%,
			rgba(255, 234, 198, 0.134) 216.94%
		);
		filter: blur(350px);
		top: 872px;
		left: 50%;
		transform: translate(-50%, 0);
		z-index: -1;
		width: 1481px;
		height: 1481px;
	}
	.decor4 {
		background: linear-gradient(
			181.87deg,
			#ffdfa8 -157.44%,
			rgba(255, 226, 176, 0.96) -157.4%,
			rgba(255, 234, 198, 0.67) 216.94%,
			rgba(250, 211, 144, 0.8) 216.94%
		);
		filter: blur(100px);
		transform: rotate(-30deg);
		width: 541px;
		height: 226px;
		left: -134px;
		top: 1536.5px;
		z-index: -2;
	}
	.decor5 {
		width: 871.21px;
		height: 871.21px;
		left: -550.84px;
		border-radius: 50%;
		top: 3843.19px;
		background: rgba(218, 77, 241, 0.4);
		filter: blur(250px);
		z-index: -1;
		transform: rotate(-130.44deg);
	}
	.decor6 {
		width: 1044.32px;
		height: 1044.32px;
		right: -850px;
		top: 4065.22px;
		background: rgba(196, 245, 233, 0.7);
		filter: blur(300px);
		transform: rotate(-130.44deg);
		z-index: -1;
	}
	.decor7 {
		width: 1481px;
		height: 1482px;
		left: 50%;
		transform: translate(-50%, 0);
		top: 6649.01px;
		background: linear-gradient(
			181.87deg,
			rgba(255, 234, 198, 0.2) -157.44%,
			rgba(255, 234, 198, 0.144) 216.94%,
			rgba(255, 234, 198, 0.134) 216.94%
		);
		filter: blur(350px);
		z-index: -1;
	}
}

ul,
ol,
li,
p,
h1,
h2,
h3,
h4,
h5,
h6 {
	margin: 0;
	padding: 0;
	list-style: none;
}
a {
	text-decoration: none;
}
* {
	margin: 0;
	padding: 0;
	box-sizing: border-box;
}
*:focus-visible {
	outline: none;
}
.container {
	max-width: $mw;
	margin: 0 auto;
	@media (max-width: $md1) {
		max-width: 1240px;
	}
	@media (max-width: $md2) {
		max-width: 1100px;
	}
	@media (max-width: $md3) {
		max-width: 950px;
	}
	@media (max-width: $md4) {
		max-width: 750px;
	}
	@media (max-width: $md5) {
		max-width: none;
		padding: 0 16px;
	}
}
//main
.btn {
	font-family: "Work Sans";
	font-style: normal;
	font-weight: 600;
	font-size: 16px;
	line-height: 1.5;
	color: #ffffff;
	padding: 12px 24px;
	background: #a53dff;
	border-radius: 4px;
	display: inline-flex;
	align-items: center;
	justify-content: center;
	transition: all 0.3s ease;
	svg {
		margin-left: 12px;
		transition: all 0.3s ease;
	}
	&:hover {
		background: #8431cc;
		transition: all 0.3s ease;
		svg {
			transform: translate(6px, 0);
			transition: all 0.3s ease;
		}
	}
}
.contact-icons {
	display: flex;
	justify-content: center;
	align-items: center;
	@media (max-width: 400px) {
		flex-wrap: wrap;
	}
}
.contact-icons-item {
	width: 48px;
	height: 48px;
	background: #ffffff;
	border-radius: 4px;
	display: inline-flex;
	align-items: center;
	justify-content: center;
	transition: all 0.3s ease;
	path {
		transition: all 0.3s ease;
	}
	&:hover {
		transition: all 0.3s ease;
		background: #a53dff;
		box-shadow: 0px 12px 64px rgba(28, 25, 25, 0.12);
		path {
			fill: #ffffff;
			transition: all 0.3s ease;
		}
	}
}
.title{
	font-family: "Work Sans";
	font-style: normal;
	font-weight: 600;
	font-size: 48px;
	text-align: center;
	line-height: 1.17;
	color: #132238;
	margin-bottom: 24px;
	@media (max-width: 480px) {
		font-size: 36px;
		margin-bottom: 12px;
	}
}
.subtitle{
	font-family: "Work Sans";
	font-style: normal;
	font-weight: 600;
	font-size: 38px;
	line-height: 1.32;
	color: #132238;
	@media (max-width: 600px) {
		font-size: 32px;
	}
	@media (max-width: 400px) {
		font-size: 28px;
	}
}
.text {
	font-family: "Work Sans";
	font-style: normal;
	font-weight: 400;
	font-size: 18px;
	line-height: 1.33;
	color: #87909d;
	@media (max-width: 600px) {
		font-size: 16px;
	}
}
.subtext {
	font-family: "Work Sans";
	font-style: normal;
	font-weight: 400;
	font-size: 16px;
	line-height: 1.5;
	color: #556070;
}

.subtext-big{
	font-family: "Work Sans";
	font-style: normal;
	font-weight: 400;
	font-size: 14px;
	line-height: 1.43;
	color: #556070;
}