.section {
   &__blog {
      padding: 100px 0 132px;
   }
}
.blog {
   &__header {
      max-width: 540px;
      width: 100%;
      margin: 0 auto 70px;
   }
   &__text {
      font-family: "Public Sans" !important;
      line-height: 1.44 !important;
      text-align: center;
   }
   &__swiper {
      margin-bottom: 24px;
      .slide {
         background: #ffffff;
         border: 1px solid #f0f1f3;
         border-radius: 8px;
         transition: all 0.3s ease;
         &:hover {
            box-shadow: 0px 12px 64px rgba(28, 25, 25, 0.12);
            transition: all 0.3s ease;
            .slide__img img {
               transform: scale(1.05);
               transition: all 0.3s ease;
            }
         }
         &__img {
            width: 100%;
            height: 226px;
            border-radius: 8px 8px 0px 0px;
            overflow: hidden;
            img {
               width: 100%;
               transition: all 0.3s ease;
               height: 100%;
            }
         }
         &__content {
            padding: 24px;
         }
         &__subtitle {
            color: #87909d !important;
            margin-bottom: 8px;
         }
         &__title {
            font-weight: 500 !important;
            color: #333333 !important;
         }
      }
   }
   .swiper-pagination {
      position: relative !important;
      bottom: 0 !important;
      display: flex;
      justify-content: center;
      align-items: center;
      &-bullet {
         width: 8px;
         height: 8px;
         opacity: 1 !important;
         background: #c3c7ce;
         transition: all .3s ease;
         &-active {
            width: 30px;
            height: 8px;
            background: #210c33;
            border-radius: 100px;
            transition: all .3s ease;
         }
      }
   }
}
