.navbar {
   background: #ffffff;
   position: fixed;
   top: 0;
   left: 0;
   right: 0;
   z-index: 98;
}
.nav {
   display: flex;
   justify-content: space-between;
   align-items: center;
   padding: 20px 0;
   &__logo {
      margin-right: auto;
      img {
         width: 208px;
         height: 56px;
      }
      @media (max-width: 400px) {
         img{
            width: 180px;
            height: 48.5px;
         }
      }
   }
   &__menu {
      @media (max-width: 992px) {
         opacity: 0;
         visibility: hidden;
         position: fixed;
         left: 0;
         top: 0;
         bottom: 0;
         right: 0;
         width: 100vw;
         height: 100vh;
         overflow-y: auto;
         display: flex;
         justify-content: center;
         align-items: center;
         z-index: 98;
         transition: all 0.5s ease;
         background-color: #ffffff;
         &::-webkit-scrollbar {
            display: none;
         }
         &.active {
            opacity: 1;
            visibility: visible;
            transition: all 0.5s ease;
         }
      }
      @media (max-width: 992px) and (max-height: 530px) {
         align-items: flex-start;
         padding: 100px 0;
      }
   }
   &__list {
      display: flex;
      justify-content: flex-end;
      align-items: center;
      @media (max-width: 992px) {
         flex-direction: column;
         justify-content: center;
      }
   }
   &__list-item {
      display: inline-flex;
      a {
         font-family: "Work Sans";
         font-style: normal;
         font-weight: 500;
         font-size: 16px;
         line-height: 1.5;
         color: #333333;
         padding: 12px 24px;
         border-radius: 4px;
         transition: all 0.3s ease;
         &:hover {
            color: #a53dff;
            transition: all 0.3s ease;
         }
         @media (max-width: 1110px) {
            padding: 12px 14px;
         }
         @media (max-width: 992px) {
            padding: 12px 24px;
            font-size: 22px;
            &:hover {
               box-shadow: none !important;
            }
         }
      }
      &.nav__list-btn a {
         background: #a53dff;
         color: #ffffff;
         transition: all 0.3s ease;
         &:hover {
            background: #8431cc;
            color: #ffffff;
            transition: all 0.3s ease;
         }
      }
   }
   .burger {
      @media (max-width: 992px) {
         width: 25.5px;
         height: 20px;
         position: relative;
         z-index: 99;
         &::after,
         &::before,
         span {
            content: "";
            position: absolute;
            width: 100%;
            height: 2px;
            left: 0;
            right: 0;
            background-color: #000000;
            transition: 0.3s ease 0s;
            transform-origin: right;
         }
         &::before {
            top: 0px;
         }
         &::after {
            bottom: 0px;
         }
         span {
            top: 50%;
            transform: translate(0, -50%);
         }
         &.active {
            &::before {
               transform: rotate(-45deg);
               transition: 0.3s ease 0s;
            }
            &::after {
               transform: rotate(45deg);
               transition: 0.3s ease 0s;
            }
            span {
               transform: translate(0, -50%) scaleX(0);
               transition: 0.3s ease 0s;
            }
         }
      }
   }
}
