.section {
   &__portfolio {
      padding: 100px 0;
   }
}
.portfolio {
   &__header {
      max-width: 577px;
      width: 100%;
      margin: 0 auto 70px;
   }
   &__text {
      text-align: center;
   }
   &__items {
      display: grid;
      grid-template-columns: repeat(3, 1fr);
      gap: 24px;
      margin-bottom: 50px;
      @media (max-width: 1110px){
         grid-template-columns: repeat(2, 1fr);
      }
      @media (max-width: 600px){
         grid-template-columns: repeat(1, 1fr);
      }
   }
   &__item {
      width: 100%;
      background: #ffffff;
      border: 1px solid #e6e8eb;
      border-radius: 8px;
   }
   &__item-img {
      width: 100%;
      height: 248px;
      border-radius: 8px 8px 0px 0px;
      overflow: hidden;
      img {
         width: 100%;
         height: 100%;
      }
      @media (max-width: 768px){
         height: 200px;
      }
      @media (max-width: 600px){
         height: auto;
      }
   }
   &__item-content {
      padding: 32px;
      @media (max-width: 768px){
         padding: 16px;
      }
   }
   &__item-subtitle {
      font-family: "Work Sans";
      font-style: normal;
      font-weight: 500;
      font-size: 12px;
      line-height: 1.33;
      color: #87909d;
      margin-bottom: 4px;
   }
   &__item-title {
      font-weight: 600 !important;
      color: #132238 !important;
      margin-bottom: 12px;
   }
   &__item-text {
      margin-bottom: 20px;
   }
   &__item-btn {
      padding: 12px 24px;
      display: inline-flex;
      align-items: center;
      justify-content: center;
      background: #ffffff;
      border: 1px solid #a53dff;
      border-radius: 4px;
      font-family: "Work Sans";
      font-style: normal;
      font-weight: 600;
      font-size: 16px;
      line-height: 1.5;
      color: #a53dff;
      transition: all 0.3s ease;
      svg {
         margin-left: 12px;
         transition: all 0.3s ease;
         path {
            transition: all 0.3s ease;
         }
      }
      &:hover {
         border: 1px solid #8431CC;
         transition: all 0.3s ease;
         color: #8431cc;
         svg {
            transform: translate(6px, 0);
            transition: all 0.3s ease;
            path {
               fill: #8431CC;
               transition: all 0.3s ease;
            }
         }
      }
   }
   &__button {
      display: flex;
      justify-content: center;
   }
}
