.section {
   &__work {
      background: #f0f1f3;
      padding: 148px 0 140px;
      position: relative;
      z-index: 0;
      &::after {
         content: "";
         position: absolute;
         top: -100px;
         left: 0;
         right: 0;
         height: 100px;
         width: 100%;
         background: #f0f1f3;
         z-index: -2;
      }
   }
}
.work {
   display: flex;
   justify-content: space-between;
   align-items: center;
   @media (max-width: 992px) {
      flex-direction: column;
      align-items: flex-start;
   }
   &__content {
      max-width: 529px;
      width: 100%;
      margin-right: 143px;
      @media (max-width: 1280px) {
         margin-right: 100px;
      }
      @media (max-width: 1110px) {
         margin-right: 30px;
      }
      @media (max-width: 992px) {
         margin-right: auto;
         margin-bottom: 40px;
      }
   }
   &__title {
     text-align: left !important;
   }
   &__text {
      color: #697484 !important;
   }
   &__items {
      display: grid;
      grid-template-columns: repeat(2, 1fr);
      column-gap: 24px;
      row-gap: 0;
      @media (max-width: 600px) {
         grid-template-columns: repeat(1, 1fr);
         row-gap: 24px;
      }
   }
   &__item {
      padding: 32px;
      background: #ffffff;
      border-radius: 12px;
      transition: all 0.3s ease;
      &:nth-child(2n + 1) {
         margin-bottom: 24px;
      }
      &:nth-child(2n + 2) {
         margin-top: 24px;
      }
      @media (max-width: 600px) {
         margin: 0 !important;
      }
      &:hover {
         box-shadow: 0px 0px 124px rgba(43, 56, 76, 0.23);
         transition: all 0.3s ease;
         .work__item-img{
            background: #FFFFFF;
            transition: all 0.3s ease;
         }
      }
   }
   &__item-img {
      background: rgba(237, 216, 255, 0.5);
      border-radius: 6px;
      display: inline-flex;
      align-items: center;
      justify-content: center;
      margin-bottom: 32px;
      width: 72px;
      height: 72px;
      transition: all 0.3s ease;
   }
   &__item-title {
      font-family: "Work Sans";
      font-style: normal;
      font-weight: 600;
      font-size: 20px;
      line-height: 1.2;
      color: #132238;
      margin-bottom: 12px;
   }
}
