.section {
   &__hero {
      padding: 226px 0 222px;
      @media (max-width: 992px) {
         padding: 160px 0 120px;
      }
      @media (max-width: 768px) {
         padding: 130px 0 120px;
      }
   }
}
.hero {
   &__container {
      display: flex;
      justify-content: space-between;
      align-items: flex-end;
      margin-bottom: 142px;
      @media (max-width: 992px) {
         flex-direction: column;
         justify-content: center;
         align-items: flex-start;
         margin-bottom: 42px;
      }
   }
   &__title {
      font-family: "Work Sans";
      font-style: normal;
      font-weight: 600;
      font-size: 72px;
      line-height: 1.17;
      color: #132238;
      max-width: 578px;
      width: 100%;
      margin-bottom: 24px;
      @media (max-width: 768px) {
         font-size: 58px;
         max-width: 520px;
         margin-bottom: 12px;
      }
      @media (max-width: 600px) {
         font-size: 42px;
         max-width: 370px;
      }
      @media (max-width: 400px) {
         font-size: 36px;
         max-width: 320px;
      }
   }
   &__text {
      color: #556070 !important;
      margin-bottom: 24px;
      max-width: 648px;
      width: 100%;
      span {
         color: #132238;
         position: relative;
         z-index: 0;
         &::after {
            content: "";
            position: absolute;
            bottom: 0;
            left: 0;
            right: 0;
            height: 50%;
            width: 100%;
            background-color: #ffc8c8;
            z-index: -1;
         }
      }
   }
   &__img {
      background: #ffffff;
      box-shadow: 0px 24px 116px rgba(43, 56, 76, 0.09);
      border-radius: 25px;
      width: 321px;
      height: 380px;
      overflow: hidden;
      margin-right: 215px;
      img {
         width: 100%;
         height: 100%;
      }
      @media (max-width: 1280px) {
         margin-right: 0;
      }
      @media (max-width: 992px) {
         margin-top: 50px;
         width: 100%;
         height: auto;
      }
   }
   &__items {
      display: grid;
      grid-template-columns: repeat(3, 1fr);
      gap: 3px;
      max-width: 648px;
      width: 100%;
      @media (max-width: 600px) {
         grid-template-columns: repeat(2, 1fr);
      }
      @media (max-width: 400px) {
         grid-template-columns: repeat(1, 1fr);
      }
   }
   &__item {
      background: rgba(237, 216, 255, 0.5);
      border: 1px solid #ffffff;
      border-radius: 6px;
      padding: 17px 12px;
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: center;
      @media (max-width: 600px) {
         &:nth-child(3n + 3) {
            grid-column: span 3;
         }
      }
      @media (max-width: 400px) {
         &:nth-child(3n + 3) {
            grid-column: auto;
         }
      }
   }
   &__item-title {
      font-family: "Work Sans";
      font-style: normal;
      font-weight: 600;
      font-size: 32px;
      line-height: 1.25;
      text-align: center;
      color: #424e60;
      margin-bottom: 8px;
   }
   &__item-text {
      text-align: center;
   }
}
