.section {
   &__user {
      position: relative;
      z-index: 1;
   }
}
.user {
   background: #ffffff;
   box-shadow: 0px 36px 105px rgba(43, 56, 76, 0.1);
   border-radius: 16px;
   display: flex;
   justify-content: space-between;
   align-items: center;
   padding: 112px;
   @media (max-width: 1280px) {
      padding: 60px;
   }
   @media (max-width: 992px) {
      flex-direction: column;
   }
   @media (max-width: 600px) {
      padding: 60px 30px;
   }
   @media (max-width: 400px) {
      padding: 40px 20px;
   }
   &__img {
      width: 424px;
      height: 468px;
      border: 1px solid #f0f1f3;
      border-radius: 10px;
      overflow: hidden;
      background: #f0f1f3;
      img {
         width: 100%;
         height: 100%;
      }
      @media (max-width: 1110px) {
         width: 380px;
         height: auto;
      }
      @media (max-width: 992px) {
         width: 100%;
      }
   }
   &__contact {
      position: absolute;
      left: 50%;
      bottom: 0;
      transform: translate(-50%, 50%);
      background: #ffffff;
      box-shadow: 0px 12px 64px rgba(28, 25, 25, 0.12);
      border-radius: 4px;
      padding: 12px;
      @media (max-width: 400px) {
         position: relative;
         left: 0;
         transform: translate(0, 0);
      }
   }
   &__content {
      &:first-child {
         position: relative;
         @media (max-width: 992px) {
            width: 100%;
         }
      }
      &:last-child {
         max-width: 536px;
         width: 100%;
         @media (max-width: 1280px) {
            max-width: 420px;
         }
         @media (max-width: 992px) {
            max-width: 100%;
            margin-top: 70px;
         }
         @media (max-width: 400px) {
            margin-top: 40px;
         }
      }
   }
   &__title {
      margin-bottom: 32px;
      @media (max-width: 600px) {
         margin-bottom: 16px;
      }
   }
   &__buttons {
      margin-top: 32px;
      display: flex;
      justify-content: flex-start;
      align-items: flex-start;
      @media (max-width: 480px) {
         flex-direction: column;
      }
   }
   &__btn {
      margin-right: 24px;
      &:last-child {
         margin-right: 0;
      }
      @media (max-width: 480px) {
         width: 100%;
         margin-right: 0;
         margin-bottom: 12px;
         &:last-child {
            margin-bottom: 0;
         }
      }
   }
   &__btn-light {
      background: #ffffff;
      border: 1px solid #a53dff;
      color: #a53dff;
      transition: all 0.3s ease;
      svg {
         margin-right: 12px;
         margin-left: 0;
         path {
            transition: all 0.3s ease;
         }
      }
      &:hover {
         transition: all 0.3s ease;
         background: #a53dff;
         color: #ffffff;
         svg{
            transform: translate(0, 0);
         }
         path {
            fill: #ffffff;
            transition: all 0.3s ease;
         }
      }
   }
}
