footer {
   background: #2B384C;
   padding: 80px 0;
   @media (max-width: 1110px){
      padding: 40px 0 ;
   }
}
.footer {
   display: flex;
   justify-content: space-between;
   align-items: center;
   @media (max-width: 1110px){
      flex-wrap: wrap;
      align-items: flex-start;
   }
   @media (max-width: 600px){
      flex-direction: column;
      align-items: center;
      justify-content: center;
   }
   &__logo {
      width: auto;
      height: auto;
      img {
         width: 208px;
         height: 56px;
      }
      @media (max-width: 1110px){
         flex: 0 1 50%;
      }
      @media (max-width: 600px){
         flex: 0 1 100%;
         margin-bottom: 20px;
         display: flex;
         justify-content: center;
      }
   }
   &__list {
      display: flex;
      justify-content: center;
      align-items: center;
      @media (max-width: 1110px){
         flex: 0 1 50%;
         width: 100%;
         flex-wrap: wrap;
         justify-content: flex-start;
         align-items: flex-start;
         margin-bottom: 40px;
      }
      @media (max-width: 600px){
         flex: 0 1 100%;
         align-items: center;
         justify-content: center;
         flex-direction: column;
      }
   }
   &__list-item {
      padding: 12px;
      a{
         transition: all .3s ease;
         color: #f0f1f3 !important;
         &:hover{
            color: rgba($color: #f0f1f3, $alpha: .8) !important;
            transition: all .3s ease;
         }
      }
      @media (max-width: 1110px){
         flex: 0 1 50%;
         width: 100%;
      }
      @media (max-width: 600px){
         flex: 0 1 100%;
         text-align: center;
      }
   }
   &__copy {
      color: #FFFFFF !important;
      @media (max-width: 1110px){
         flex: 0 1 100%;
         width: 100%;
         text-align: center;
      }
   }
}
