.contact {
   background: #ffffff;
   box-shadow: 0px 59px 124px rgba(0, 0, 0, 0.12);
   border-radius: 15px;
   padding: 88px;
   display: flex;
   justify-content: space-between;
   align-items: center;
   @media (max-width: 992px){
      flex-direction: column;
   }
   @media (max-width: 768px){
      padding: 60px 30px;
   }
   @media (max-width: 400px){
      padding: 40px 20px;
   }
   &__content {
      width: 100%;
      &:first-child {
         max-width: 481px;
         margin-right: 40px;
      }
      &:last-child {
         max-width: 560px;
      }
      @media (max-width: 992px){
         max-width: 100% !important;
         margin-right: 0 !important;
         &:last-child{
            margin-top: 50px;
         }
      }
      @media (max-width: 400px){
         &:last-child{
            margin-top: 38px;
         }
      }
   }
   &__title {
      margin-bottom: 16px;
      @media (max-width: 600px){
         margin-bottom: 8px;
      }
   }
   &__items {
      margin: 35px 0;
      max-width: 336px;
      width: 100%;
      @media (max-width: 992px){
         margin: 15px 0;
      }
   }
   &__item {
      display: flex;
      align-items: flex-start;
      border-radius: 10px;
      padding: 24px;
      transition: all 0.3s ease;
      &:hover {
         box-shadow: 0px 12px 64px rgba(28, 25, 25, 0.12);
         transition: all 0.3s ease;
         .contact__item-img {
            background: #a53dff;
            transition: all 0.3s ease;
            path {
               transition: all 0.3s ease;
               fill: #ffffff;
            }
         }
      }
      @media (max-width: 400px){
         padding: 12px;
      }
   }
   &__item-img {
      min-width: 48px;
      max-width: 48px;
      width: 100%;
      height: 48px;
      background: rgba(237, 216, 255, 0.5);
      border-radius: 4px;
      display: inline-flex;
      justify-content: center;
      align-items: center;
      margin-right: 13px;
      transition: all 0.3s ease;
      path {
         transition: all 0.3s ease;
      }
   }
   &__item-subtitle {
      color: #424e60 !important;
      margin-bottom: 4px;
   }
   &__item-title {
      font-weight: 500 !important;
      color: #132238 !important;
      @media (max-width: 400px){
         word-break: break-all;
      }
   }
   &__icons{
      justify-content: flex-start !important;
   }
   &__icons-icon {
      margin-right: 12px;
      &:last-child {
         margin-right: 0;
      }
      @media (max-width: 400px){
         margin-bottom: 12px;
      }
   }
   &__form {
      margin-top: 50px;
      display: flex;
      flex-wrap: wrap;
      @media (max-width: 992px){
         margin-top: 25px;
      }
   }
   &__form-input {
      width: 100%;
      padding: 0 0 14px;
      border: none;
      background: none;
      border-bottom: 1.5px solid #e6e8eb;
      margin-bottom: 24px;
      flex: 0 1 100%;
      color: #a53dff !important;
      transition: all 0.3s ease;
      &::placeholder {
         color: #697484 !important;
         transition: all 0.3s ease;
      }
      &::-moz-placeholder {
         color: #697484 !important;
         opacity: 1;
         transition: all 0.3s ease;
      }
      &:focus-visible {
         border-bottom: 1.5px solid #a53dff;
         transition: all 0.3s ease;
         &::placeholder {
            color: #a53dff !important;
            transition: all 0.3s ease;
         }
         &::-moz-placeholder {
            color: #a53dff !important;
            opacity: 1;
            transition: all 0.3s ease;
         }
      }
      &:nth-child(4) {
         flex: 0 1 calc(35% - 24px);
         margin-right: 24px;
         @media (max-width: 480px){
            margin-right: 0;
            flex: 0 1 100%;
         }
      }
      &:nth-child(5) {
         flex: 0 1 65%;
         @media (max-width: 480px){
            flex: 0 1 100%;
         }
      }
   }
   &__form-textarea {
      resize: none;
      height: 48px;
   }
   &__form-btn {
      border: none;
      cursor: pointer;
   }
}
