.section {
   &__services {
      padding: 150px 0;
      background: #f0f1f3;
   }
}
.accordion {
   position: relative;
   &__item {
      display: flex;
      justify-content: space-between;
      align-items: center;
      flex-direction: row-reverse;
      margin-bottom: 24px;
      @media (max-width: 992px) {
         flex-direction: column;
      }
      &.show {
         @media (max-width: 992px){
            box-shadow: 0px 32px 96px rgba(28, 25, 25, 0.16);
         }
         .accordion__content {
            opacity: 1;
            visibility: visible;
            transition: all 0.3s ease;
            @media (max-width: 992px){
               height: auto;
               padding: 32px;
            }
            @media (max-width: 480px){
               padding: 24px;
            }
         }
         .accordion__header {
            border-left: 5px solid #a53dff;
            padding: 32px 32px 32px 27px;
            transition: all 0.3s ease;
            @media (max-width: 992px){
               border-radius: 6px 6px 0 0;
            }
            @media (max-width: 480px){
               padding: 24px 24px 24px 19px;
            }
         }
      }
      &:last-child {
         margin-bottom: 0;
      }
   }
   &__header {
      max-width: 648px;
      width: 100%;
      padding: 32px;
      background: #ffffff;
      box-shadow: 0px 32px 96px rgba(28, 25, 25, 0.16);
      border-radius: 6px;
      transition: all 0.3s ease;
      cursor: pointer;
      @media (max-width: 1280px) {
         max-width: 550px;
      }
      @media (max-width: 1110px) {
         max-width: 450px;
      }
      @media (max-width: 992px) {
         max-width: 100%;
         box-shadow: none;
      }
      @media (max-width: 480px){
         padding: 24px;
      }
   }
   &__header-title {
      font-family: "Work Sans";
      font-style: normal;
      font-weight: 600;
      font-size: 24px;
      line-height: 100%;
      color: #132238;
      margin-bottom: 16px;
      @media (max-width: 480px){
         font-size: 20px;
         margin-bottom: 8px;
      }
   }
   &__header-text {
      font-family: "Poppins" !important;
      @media (max-width: 480px){
         font-size: 14px !important;
      }
   }
   &__content {
      max-width: 529px;
      width: 100%;
      opacity: 0;
      visibility: hidden;
      position: absolute;
      left: 0;
      top: 50%;
      transform: translate(0, -50%);
      transition: all 0.3s ease;
      @media (max-width: 1110px) {
         max-width: 450px;
      }
      @media (max-width: 992px) {
         max-width: 100%;
         position: relative;
         top: 0;
         transform: translate(0, 0);
         background: #ffffff;
         border-radius: 6px;
         height: 0;
         padding: 0 32px;
         border-radius: 0 0 6px 6px;
      }
      @media (max-width: 480px){
         padding: 0 24px;
      }
   }
   &__content-title {
      text-align: left !important;
      color: #333333 !important;
      margin-bottom: 24px;
      @media (max-width: 480px){
         margin-bottom: 8px !important;
         font-size: 42px !important;
      }
      @media (max-width: 400px){
         font-size: 36px !important;
      }
   }
   &__content-text {
      margin-bottom: 50px;
      @media (max-width: 480px){
         margin-bottom: 25px;
      }
   }
}
