.section {
   &__clients {
      padding: 100px 0;
   }
}
.clients {
   &__header {
      max-width: 577px;
      width: 100%;
      margin: 0 auto 24px;
   }
   &__text {
      text-align: center;
   }
   &__items {
      display: flex;
      flex-wrap: wrap;
      align-items: center;
      justify-content: center;
   }
   &__item {
      flex: 0 1 calc(16.6% - 24px);
      width: 100%;
      display: inline-flex;
      align-items: center;
      justify-content: center;
      margin-right: 12px;
      margin-left: 12px;
      margin-bottom: 24px;
      height: 200px;
      @media (max-width: 1110px){
         flex: 0 1 calc(20% - 24px);
         height: 130px;
      }
      @media (max-width: 992px){
         flex: 0 1 calc(25% - 24px);
      }
      @media (max-width: 768px){
         flex: 0 1 calc(33% - 24px);
      }
      @media (max-width: 600px){
         flex: 0 1 calc(50% - 24px);
      }
      @media (max-width: 350px){
         margin-right: 0;
         flex: 0 1 100%;
      }
      img {
        width: auto;
        object-fit: contain;
        height: auto;
      }
   }
}
